* {
  margin: 0;
  padding: 0; }

.modal {
  transition: 0.3s all;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 650px !important;
  height: 700px;
  color: white;
  transform: translate(-50%, -50%);
  background-color: #32393d;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
  padding: 40px;
  outline: "none";
  margin: "auto";
  margin-top: "15rem";
  border-radius: 12px; }
  .modal:focus {
    outline: none; }

.bg-img {
  background: url("http://localhost:8080/assets/landing-background.jpg") no-repeat center center fixed;
  background-size: cover;
  overflow: hidden;
  width: 100%;
  height: 100vh; }

.input {
  font-size: 24px; }

.grid {
  min-height: 100% !important;
  flex: 1; }

.marginSides {
  margin: 0 20px !important; }

.customSignupCard {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 500px;
  min-height: 540px;
  padding: 40px;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  background-color: #32393d;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3); }

.gameCheck {
  margin: 10px;
  cursor: pointer;
  position: relative;
  width: 100px;
  height: 100px;
  border-radius: 12px;
  transition: all 0.3s; }
  .gameCheck:hover {
    color: rgba(255, 255, 255, 0.3); }
  .gameCheck > div {
    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    border-radius: 12px;
    transition: background-color 0.3s; }
    .gameCheck > div:hover {
      background-color: rgba(120, 135, 144, 0.2); }
    .gameCheck > div > div {
      margin: 3px;
      position: absolute;
      top: 0px;
      bottom: 0px;
      left: 0px;
      right: 0px;
      border-radius: 12px;
      border: #32393d solid 4px;
      transition: all 0.3s;
      display: flex;
      align-items: center;
      justify-content: center; }
      .gameCheck > div > div i {
        font-size: 3em;
        color: rgba(255, 255, 255, 0);
        transition: color 0.3s; }
  .gameCheck_selected > div {
    background-color: rgba(0, 0, 0, 0); }
    .gameCheck_selected > div > div {
      background-color: rgba(50, 57, 61, 0.7); }
      .gameCheck_selected > div > div i {
        color: white; }
